import React from 'react'
import PropTypes from 'prop-types'

import Header from '../components/header'
import './index.scss'

const Layout = ({ children }) => (
  <div>
    <Header />
    <main>
      {children()}
    </main>
  </div>
)

Layout.propTypes = {
  children: PropTypes.func,
}

export default Layout


